/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
	box-sizing: border-box;
}

body {
	background: #fff;
	font-family: Arial, Helvetica, sans-serif;
}

a {
	text-decoration: none;
}

header {
	height: 90px;
	margin-bottom: 20px;
}

header .headerLogo {
	display: none;
}

header .flexHeaderImg {
	display: flex;
	flex-direction: row;
}

header .flexHeaderImg .headerLogoMobile {
	padding: 20px;
}

header .flexHeaderImg .headerLogoMobile img {
	max-width: 100px;
}

header .flexHeaderImg .hamburger {
	padding: 20px;
	text-align: right;
	width: 80%;
}

header .flexHeaderImg .hamburger img {
	max-width: 30px;
}

header .overlayNavMobile {
	display: none;
	position: fixed;
	top: 0%;
	left: -500px;
	width: 100%;
	height: 100%;
	z-index: 2;
	background: #ccc;
}

header .overlayNavMobile .flexOverlayHeader {
	display: flex;
	justify-content: space-around;
}

header .overlayNavMobile .flexOverlayHeader .headerLogoMobile {
	text-align: left;
	padding: 20px;
}

header .overlayNavMobile .flexOverlayHeader .headerLogoMobile img {
	max-width: 100px;
}

header .overlayNavMobile .flexOverlayHeader .closeBtn {
	padding: 20px;
	text-align: right;
}

header .overlayNavMobile .flexOverlayHeader .closeBtn img {
	width: 8%;
}

header .overlayNavMobile .dlBtn {
	text-align: center;
}

header .overlayNavMobile .dlBtn img {
	max-width: 50px;
}

header .overlayNavMobile .dlBtn span {
	display: block;
	color: white;
	text-align: center;
	margin-top: 5px;
}

header .overlayNavMobile ul {
	text-align: center;
	padding: 20px;
}

header .overlayNavMobile ul li {
	margin: 25px;
}

header .overlayNavMobile ul a {
	color: white;
}

header .overlayNavMobile ul a:hover {
	color: #76BC21;
}

header .sideNavDesktop {
	display: none;
}

.content {
	padding: 20px;
	margin: 40px 0px;
}

.content h2 {
	color: #76BC21;
	margin: 10px 0px;
	font-size: 1.2rem;
}

.content h3 {
	color: #76BC21;
}

.content p {
	color: #808285;
	line-height: 1.2;
	margin: 15px 0px;
}

.content .col-2 {
	width: 100%;
}

.content .col-2 .flexWrapBoardMembers {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}

.content .col-2 .flexWrapBoardMembers .boardMembers {
	width: 50%;
}

.content .col-2 .flexWrapBoardMembers .boardMembers p {
	margin: 5px 0px;
}

.content .col-2 .flexWrapBoardMembers .boardMembers p:nth-child(even) {
	color: #ccc;
}

.content .needSection {
	margin: 50px 0px;
}

.content .needSection .needTilesWrap {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	text-align: center;
}

.content .needSection .needTilesWrap .needTiles {
	width: 48%;
	margin-right: 2%;
	margin-bottom: 2%;
	background: #76BC21;
}

.content .needSection .needTilesWrap .needTiles p {
	font-size: .7rem;
	color: white;
	height: 30px;
	font-weight: bold;
	width: 70%;
	margin: 12px auto;
}

.content .needSection .needTilesWrap .needTiles span {
	display: block;
	font-size: .6rem;
	color: white;
	height: 30px;
	text-align: center;
	margin: 12px auto;
	width: 75%;
	line-height: 1.2;
}

.content .needSection .needTilesWrap .needTiles img {
	width: 60%;
	margin: 10px 0px;
}

.content .needSection .needTilesWrap .needTiles .img3Tile {
	width: 25%;
}

.content .needSection .needTilesWrap .needTiles .img2Tile {
	width: 40%;
}

.content .needSection .needTilesWrap .needTiles .img6Tile {
	width: 40%;
}

.content .needSection .needTilesWrap .tile3 p {
	width: 50%;
}

.content .needSection .needTilesWrap .tile4 p {
	width: 80%;
}

.content .needSection .needTilesWrap .tile5 p {
	width: 80%;
}

.content .objSection {
	margin: 50px 0px;
}

.content .objSection .objTilesWrap {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	text-align: center;
	position: relative;
}

.content .objSection .objTilesWrap .objTiles {
	height: 160px;
	width: 48%;
	margin-right: 2%;
	margin-bottom: 2%;
	background: #76BC21;
}

.content .objSection .objTilesWrap .objTiles p {
	font-size: .9rem;
	color: white;
	width: 80%;
	margin: auto;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.content .goalsSection {
	margin: 50px 0px;
}

.content .goalsSection .goalTilesWrap {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	text-align: center;
	position: relative;
}

.content .goalsSection .goalTilesWrap .goalTiles {
	height: 160px;
	width: 48%;
	margin-right: 2%;
	margin-bottom: 2%;
	background: #76BC21;
}

.content .goalsSection .goalTilesWrap span {
	font-size: 1.4rem;
}

.content .goalsSection .goalTilesWrap p {
	font-size: .8rem;
	color: white;
	width: 90%;
	margin: auto;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
}

.content .contactSection form {
	display: flex;
	flex-direction: column;
}

.content .contactSection form input {
	widows: 48%;
}

@media only screen and (min-width: 512px) {
	.content .needSection .needTilesWrap .needTiles p {
		font-size: .8rem;
	}
	.content .needSection .needTilesWrap .needTiles span {
		font-size: .7rem;
	}
	.content .goalsSection .goalTilesWrap p {
		font-size: .9rem;
	}
}

@media only screen and (min-width: 700px) {
	.content .needSection .needTilesWrap .needTiles {
		width: 31%;
	}
}
